import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { FaGithub, FaExternalLinkAlt, FaReact, FaNodeJs, FaPython } from 'react-icons/fa';
import { SiTensorflow, SiMongodb, SiFirebase, SiNextdotjs } from 'react-icons/si';

const projects = [
  {
    id: 1,
    title: "CineFlix",
    description: "A modern movie streaming platform with AI-powered recommendations",
    tags: ["Frontend", "AI", "Full-Stack"],
    techStack: ["React", "Node.js", "MongoDB"],
    github: "https://github.com/yourusername/cineflix",
    live: "https://cineflix-demo.com",
  },
  {
    id: 2,
    title: "FitTrack Pro",
    description: "AI-powered fitness tracking and workout planning application",
    tags: ["AI", "Full-Stack"],
    techStack: ["React", "Python", "TensorFlow"],
    github: "https://github.com/yourusername/fittrack",
    live: "https://fittrack-pro.com",
  },
  {
    id: 3,
    title: "TaskFlow Master",
    description: "Productivity and task management tool with real-time collaboration",
    tags: ["Frontend", "Full-Stack"],
    techStack: ["React", "Firebase", "Node.js"],
    github: "https://github.com/yourusername/taskflow",
    live: "https://taskflow-master.com",
  },
  {
    id: 4,
    title: "CarRental Hub",
    description: "Modern car rental platform with real-time availability",
    tags: ["Frontend", "Full-Stack"],
    techStack: ["Next.js", "MongoDB", "Node.js"],
    github: "https://github.com/yourusername/carrental",
    live: "https://carrental-hub.com",
  }
];

const techIcons = {
  "React": <FaReact className="text-[#61DAFB]" />,
  "Node.js": <FaNodeJs className="text-[#339933]" />,
  "Python": <FaPython className="text-[#3776AB]" />,
  "TensorFlow": <SiTensorflow className="text-[#FF6F00]" />,
  "MongoDB": <SiMongodb className="text-[#47A248]" />,
  "Firebase": <SiFirebase className="text-[#FFCA28]" />,
  "Next.js": <SiNextdotjs className="text-white" />
};

const ProjectShowcase = () => {
  const [selectedTag, setSelectedTag] = useState('All');
  const tags = ['All', 'Frontend', 'Full-Stack', 'AI'];

  const filteredProjects = selectedTag === 'All'
    ? projects
    : projects.filter(project => project.tags.includes(selectedTag));

  const containerVariants = {
    hidden: { opacity: 0 },
    show: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1,
        delayChildren: 0.3
      }
    }
  };

  const cardVariants = {
    hidden: { opacity: 0, y: 20 },
    show: { 
      opacity: 1, 
      y: 0,
      transition: {
        type: "spring",
        bounce: 0.4
      }
    }
  };

  return (
    <section className="min-h-screen py-12 px-4 md:px-8">
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.6 }}
        className="container mx-auto"
      >
        <motion.h2 
          initial={{ y: -20, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ delay: 0.2 }}
          className="text-4xl font-bold mb-12 text-center"
        >
          Featured Projects
        </motion.h2>
        
        <motion.div 
          className="flex justify-center flex-wrap gap-4 mb-12"
          initial={{ y: 20, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ delay: 0.4 }}
        >
          {tags.map((tag, index) => (
            <motion.button
              key={tag}
              onClick={() => setSelectedTag(tag)}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              className={`px-6 py-2 rounded-full transition-all duration-300 ${
                selectedTag === tag
                  ? 'bg-primary text-white shadow-glow'
                  : 'bg-darker text-gray-400 hover:text-white hover:bg-darker/80'
              }`}
              initial={{ opacity: 0, x: -20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: 0.1 * index }}
            >
              {tag}
            </motion.button>
          ))}
        </motion.div>

        <motion.div 
          variants={containerVariants}
          initial="hidden"
          animate="show"
          className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-8"
        >
          <AnimatePresence mode="wait">
            {filteredProjects.map(project => (
              <motion.div
                key={project.id}
                variants={cardVariants}
                layout
                layoutId={`project-${project.id}`}
                whileHover={{ y: -5, transition: { duration: 0.2 } }}
                className="group relative bg-darker p-6 rounded-xl overflow-hidden"
              >
                {/* Background Pattern with Animation */}
                <motion.div 
                  className="absolute inset-0 opacity-5 group-hover:opacity-10"
                  animate={{
                    backgroundPosition: ["0% 0%", "100% 100%"],
                  }}
                  transition={{
                    duration: 20,
                    repeat: Infinity,
                    repeatType: "reverse",
                  }}
                >
                  <svg width="100%" height="100%" className="text-primary">
                    <pattern id={`pattern-${project.id}`} x="0" y="0" width="16" height="16" patternUnits="userSpaceOnUse">
                      <circle cx="1" cy="1" r="1" fill="currentColor" />
                    </pattern>
                    <rect width="100%" height="100%" fill={`url(#pattern-${project.id})`} />
                  </svg>
                </motion.div>

                <motion.div 
                  className="relative z-10"
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  transition={{ delay: 0.2 }}
                >
                  {/* Project Icon with Hover Animation */}
                  <motion.div 
                    whileHover={{ rotate: 360, scale: 1.1 }}
                    transition={{ duration: 0.3 }}
                    className="w-12 h-12 mb-4 rounded-lg bg-primary/10 flex items-center justify-center text-primary"
                  >
                    {techIcons[project.techStack[0]]}
                  </motion.div>

                  <h3 className="text-xl font-bold mb-2 group-hover:text-primary transition-colors">
                    {project.title}
                  </h3>
                  
                  <p className="text-gray-400 mb-4">{project.description}</p>
                  
                  {/* Tags */}
                  <div className="flex flex-wrap gap-2 mb-4">
                    {project.tags.map(tag => (
                      <span 
                        key={tag} 
                        className="px-2 py-0.5 text-xs rounded-full bg-primary/10 text-primary"
                      >
                        {tag}
                      </span>
                    ))}
                  </div>

                  {/* Tech Stack with Individual Hover Effects */}
                  <div className="flex gap-3 mb-4">
                    {project.techStack.map((tech, index) => (
                      <motion.span 
                        key={tech}
                        whileHover={{ scale: 1.2, rotate: [0, -10, 10, 0] }}
                        transition={{ duration: 0.3 }}
                        className="text-xl opacity-50 hover:opacity-100"
                      >
                        {techIcons[tech]}
                      </motion.span>
                    ))}
                  </div>

                  {/* Animated Links */}
                  <motion.div 
                    className="flex gap-4 mt-auto"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.3 }}
                  >
                    <a
                      href={project.live}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="text-sm text-primary hover:text-primary-light flex items-center gap-2"
                    >
                      <FaExternalLinkAlt /> Live Demo
                    </a>
                    <a
                      href={project.github}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="text-sm text-primary hover:text-primary-light flex items-center gap-2"
                    >
                      <FaGithub /> View Code
                    </a>
                  </motion.div>
                </motion.div>

                {/* Enhanced Hover Gradient */}
                <motion.div 
                  className="absolute inset-0 bg-gradient-to-r from-primary/0 via-primary/5 to-primary/0 opacity-0 group-hover:opacity-100"
                  initial={{ x: '-100%' }}
                  whileHover={{ x: '100%' }}
                  transition={{ duration: 1, repeat: Infinity }}
                />
              </motion.div>
            ))}
          </AnimatePresence>
        </motion.div>
      </motion.div>
    </section>
  );
};

export default ProjectShowcase;
